@import url(https://fonts.googleapis.com/css2?family=Jost:ital,wght@0,400;0,600;0,700;1,800&display=swap);
body {
  margin: 0;
  background: #f4f2f5 !important;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  font-family: "Jost", sans-serif;
}

.base-container {
  margin-left: 10%;
  margin-right: 10%;
}

.base-padding {
  padding-top: 100px;
  padding-bottom: 100px;
}

.bottom-padding {
  padding-bottom: 100px;
}

.flex-row {
  display: flex;
  flex-direction: row;
}

.flex-col {
  display: flex;
  flex-direction: column;
}

.align-center {
  align-items: center;
}

.align-start {
  align-items: flex-start;
}

.justify-center {
  justify-content: center;
}

.justify-btw {
  justify-content: space-between;
}

.grid-2 {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 85px;
  grid-row-gap: 50px;
}

.grid-3 {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 85px;
  grid-row-gap: 50px;
}

input {
  background: #ffffff;
  border: 1px solid #007d7f;
  box-sizing: border-box;
  border-radius: 10px;
  width: 350px;
  height: 60px;
  padding: 20px 10px;

  font-style: normal;
  font-weight: normal;
  font-size: 24px;
  line-height: 35px;
  font-feature-settings: "ss01" on;

  color: #626262;
}

textarea {
  background: #ffffff;
  border: 1px solid #007d7f;
  box-sizing: border-box;
  border-radius: 10px;
  width: 350px;
  height: 250px;
  padding: 10px 10px;

  font-style: normal;
  font-weight: normal;
  font-size: 24px;
  line-height: 35px;

  color: #626262;
}

@media screen and (max-width: 1500px) {
  .base-container {
    margin-left: 7%;
    margin-right: 7%;
  }

  .base-padding {
    padding-top: 80px;
    padding-bottom: 80px;
  }

  .bottom-padding {
    padding-bottom: 80px;
  }

  .grid-2 {
    grid-gap: 60px;
    grid-row-gap: 50px;
  }

  .grid-3 {
    grid-gap: 60px;
  }
}

@media screen and (max-width: 900px) {
  .base-container {
    margin-left: 5%;
    margin-right: 5%;
  }

  .base-padding {
    padding-top: 50px;
    padding-bottom: 50px;
  }

  .bottom-padding {
    padding-bottom: 50px;
  }

  .grid-2 {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 40px;
    grid-row-gap: 30px;
  }

  .grid-3 {
    grid-gap: 40px;
  }
}

header {
    padding-top: 15px;
    padding-bottom: 15px;

    box-sizing: border-box;
    box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);

    background: white;
}

header .header-content {
    width: 86%;
}

header .burger {
    display: none;
}

header a {
    margin-left: 17px;
    margin-right: 17px;

    font-size: 20px;
    text-decoration: none;
    color: #1f1e16;
    transition: 0.2s;
}

header nav a:hover {
    color: #9b0a00;
    text-decoration: none;
    box-shadow: 0 -2px 0 inset #9b0a00;
    transition: 0.2s;
}

.active-about {
    color: #9b0a00;
    font-weight: 600;
}

.active-sexblog {
    color: #63308f;
    font-weight: 600;
}

.active-quiz {
    color: #f7c510;
    font-weight: 600;
}

.active-parentsblog {
    color: #f16822;
    font-weight: 600;
}

.active-stories {
    color: #75bf43;
    font-weight: 600;
}

.active-faq {
    color: #32bebf;
    font-weight: 600;
}

.active-coach {
    color: #1f1e16;
    font-weight: 600;
}

header img {
    width: 130px;
}

header .loclization {
    font-style: normal;
    font-weight: bold;
    font-size: 20px;
    line-height: 29px;
}

header .loclization .slash {
    margin: 0 10px;
}

header button {
    width: 170px;
    height: 40px;

    background: #9b0a00;
    border-radius: 64px;
    border: none;

    font-style: normal;
    font-weight: 500;
    font-size: 20px;
    line-height: 29px;

    text-align: center;

    color: #ffffff;

    text-shadow: 0px 0px 2px rgba(0, 0, 0, 0.25);
    transition: 0.2s;
}

header button:hover {
    height: 45px;
    font-weight: 600;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    transition: 0.2s;
}

@media screen and (max-width: 1500px) {
    header a {
        margin-left: 15px;
        margin-right: 15px;

        font-size: 16px;
    }

    header img {
        width: 110px;
    }

    header .loclization {
        font-size: 18px;
    }

    header .loclization .slash {
        margin: 0 8px;
    }

    header button {
        width: 150px;
        height: 35px;
        font-size: 18px;
    }

    header button:hover {
        height: 38px;
        font-weight: 600;
        box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
        transition: 0.2s;
    }
}

@media screen and (max-width: 1500px) {
    header a {
        margin-left: 8px;
        margin-right: 8px;

        font-size: 16px;
        text-decoration: none;
        color: #1f1e16;
        transition: 0.2s;
    }
}

@media screen and (max-width: 900px) {
    header .flex-row {
        flex-direction: column !important;
        align-items: flex-start;
    }

    header .header-content {
        flex-direction: column;
        transition: 0.2s;
    }

    header .logo-burger {
        width: 100%;
        flex-direction: row !important;
        align-items: center;
    }

    header .burger-container {
        width: 28px;
        height: 28px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }

    header .burger {
        position: relative;
        display: block;
        background: #1f1e16;
        width: 28px;
        height: 3px;
        box-shadow: 0 6px 0 #1f1e16, 0 -6px 0 #1f1e16;
        transition: 0.2s;
    }

    header .burger .x-line {
        background: #1f1e16;
        width: 28px;
        height: 3px;
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        transform: rotate(-90deg);
        transition: 0.5s;
    }

    header a {
        margin: 5px 0;
    }

    header .loclization {
        flex-direction: row !important;
        margin-top: 20px;
        margin-bottom: 20px;
    }
}

.sex-blog-big-card {
  width: 100%;
