.quiz-container {
  width: 1000px;
  background: #ffffff;
  box-shadow: 0 0 8px rgba(0, 0, 0, 0.25);
  border-radius: 10px;
}

.quiz-container h2 {
  padding: 0 100px;
  text-align: center;
}

.quiz-container .awssld--active {
  min-height: 100%;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
}

.quiz-container .awssld__content {
  min-height: 100%;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
}

.quiz-container .awssld__container {
  min-height: 100%;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
}

.quiz-container .awssld--wrapper {
  min-height: 100%;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
}

.quiz-container .awssld {
  min-height: 100%;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
}

.quiz-container .answers-container {
  display: grid;
  grid-gap: 40px;
  grid-template-columns: 1fr 1fr;
  margin-top: 50px;
}

.quiz-container .answers-container .answer {
  width: 320px;
  height: 65px;
  padding: 0 10px;
  text-align: center;
  background: #f7c510;
  box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.25);
  border-radius: 10px;

  font-style: normal;
  font-weight: normal;
  font-size: 20px;
  line-height: 25px;

  display: flex;
  justify-content: center;
  align-items: center;

  color: #000000;
  cursor: pointer;
  transition: 0.2s;
}

.quiz-container .answers-container .answer:hover {
  transform: scale(1.05, 1.05);
  transition: 0.2s;
}

.quiz-container .question-description {
  margin: 50px 130px 0 130px;
  padding: 20px;
  background-color: #edf7ee;
  font-style: normal;
  font-weight: normal;
  font-size: 21px;
  line-height: 30px;

  text-align: center;

  color: #1f4721;
  -webkit-animation-name: show-it;
          animation-name: show-it;
  -webkit-animation-duration: 0.4s;
          animation-duration: 0.4s;
}

.quiz-container .question-wrong-description {
  margin: 50px 130px 0 130px;
  padding: 20px;
  background-color: #fff4e5;
  font-style: normal;
  font-weight: normal;
  font-size: 21px;
  line-height: 30px;

  text-align: center;

  color: #9b0a00;
  -webkit-animation-name: show-it;
          animation-name: show-it;
  -webkit-animation-duration: 0.4s;
          animation-duration: 0.4s;
}

@-webkit-keyframes show-it {
  from {
    transform: scale(0, 0);
  }
  to {
    transform: scale(1, 1);
  }
}

@keyframes show-it {
  from {
    transform: scale(0, 0);
  }
  to {
    transform: scale(1, 1);
  }
}

.quiz-container .awssld__content {
  border-radius: 10px;
  background: none;
}

@media screen and (max-width: 900px) {
  .quiz-container {
    width: 100%;
    height: 700px;
  }

  .quiz-container h2 {
    width: 100%;
    padding: 0 40px;
    font-size: 25px;
  }

  .quiz-block {
    width: 100%;
    height: 100px;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
  }

  .quiz-container .answers-container {
    grid-gap: 20px;
    grid-template-columns: 1fr;
    margin-top: 30px;
  }

  .quiz-container .answers-container .answer {
    width: 300px;
    height: 60px;
    font-size: 20px;
    line-height: 20px;
  }

  .quiz-container .question-description {
    margin: 20px;
    padding: 15px 10px;
    font-size: 18px;
    line-height: 25px;
  }

  .quiz-container .question-wrong-description {
    margin: 20px;
    padding: 10px;
    font-size: 18px;
    line-height: 25px;
  }

  @-webkit-keyframes show-it {
    from {
      transform: scale(0, 0);
    }
    to {
      transform: scale(1, 1);
    }
  }

  @keyframes show-it {
    from {
      transform: scale(0, 0);
    }
    to {
      transform: scale(1, 1);
    }
  }

  .quiz-container .awssld {
    height: 100%;
  }

  .quiz-container .awssld__content {
    height: 100%;
  }
}

