footer {
  background: #30343e;
  color: white;

  padding-top: 80px;
  padding-bottom: 80px;
}

footer .footer-name {
  width: 621px;
}

footer h2 {
  font-style: normal;
  font-weight: 800;
  font-size: 30px;
  line-height: 43px;

  text-shadow: 0px 0px 4px rgba(0, 0, 0, 0.25);

  margin-bottom: 30px;
}

footer h3 {
  font-style: normal;
  font-weight: 800;
  font-size: 30px;
  line-height: 43px;

  text-shadow: 0px 0px 4px rgba(0, 0, 0, 0.25);

  margin-bottom: 20px;
}

footer p {
  font-style: normal;
  font-weight: normal;
  font-size: 24px;
  line-height: 35px;

  text-shadow: 0px 0px 4px rgba(0, 0, 0, 0.25);
}

footer a {
  font-style: normal;
  font-weight: normal;
  font-size: 20px;
  line-height: 29px;
  box-sizing: unset;

  color: #ffffff;

  margin-bottom: 10px;
  transition: 0.2s;
}

footer .footer-links a:hover {
  text-decoration: none;
  box-shadow: 0 -2px 0 inset #ffffff;
  color: white;
  transition: 0.2s;
}

footer .footer-social {
  display: grid;
  grid-template-columns: 55px 55px;
  grid-gap: 20px;
}

footer .footer-icon {
  justify-self: center;
  align-self: center;
  width: 55px;
  height: 55px;
  background: white;
  margin: 0;

  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  border: none;

  font-size: 30px;

  color: #30343e;
  transition: 0.2s;
}

footer .footer-icon:hover {
  font-size: 35px;
  box-shadow: 0 0 7px white;
  transition: 0.2s;
}

footer .footer-phone {
  transform: rotateZ(-45deg);
}

@media screen and (max-width: 1500px) {
  footer {
    padding-top: 60px;
    padding-bottom: 60px;
  }

  footer img {
    height: 200px;
  }

  footer .footer-name {
    width: 500px;
  }

  footer h2 {
    font-size: 26px;
    line-height: 40px;
  }

  footer h3 {
    font-size: 25px;
    line-height: 43px;
  }

  footer p {
    font-size: 20px;
    line-height: 30px;
  }

  footer a {
    font-size: 18px;
    line-height: 25px;
  }

  footer .footer-social {
    grid-template-columns: 45px 45px;
  }

  footer .footer-icon {
    width: 45px;
    height: 45px;
    font-size: 25px;
  }

  footer .footer-icon:hover {
    font-size: 30px;
  }
}

@media screen and (max-width: 900px) {
  footer {
    padding-top: 40px;
    padding-bottom: 40px;
  }

  footer .footer-conatiner {
    flex-direction: column !important;
  }

  footer img {
    height: 150px;
    margin-bottom: 20px;
  }

  footer .footer-name {
    margin-top: 20px;
    max-width: 100%;
  }

  footer h2 {
    max-width: 100%;
    font-size: 25px;
    line-height: 40px;
  }

  footer h3 {
    margin-top: 20px;
    max-width: 100%;
    font-size: 25px;
    line-height: 43px;
  }

  footer p {
    max-width: 100%;
    font-size: 20px;
    line-height: 30px;
  }

  footer a {
    max-width: 100%;
    font-size: 18px;
    line-height: 25px;
  }

  footer .footer-social-container {
    align-items: flex-start;
    margin-top: 20px;
  }

  footer .footer-social {
    grid-template-columns: 45px 45px 45px 45px;
  }

  footer .footer-icon {
    width: 45px;
    height: 45px;
    font-size: 25px;
  }

  footer .footer-icon:hover {
    font-size: 30px;
  }
}
